import React, { useEffect, useRef } from "react";
import dotspattern from "../../assets/images/dots-pattern.png";
import tringleicon from "../../assets/images/pink-tringleicon.png";
import { useIsInViewport } from "../../helpers/helper";
import TypeWriterEffect from "../TypeWriterEffect/TypeWriterEffect";
import DotsAnimation from "../DotsAnimation/DotsAnimation";

function Gravitars({ setActiveLink, activeLink }) {
  const viewportRef = useRef(null);
  const isInViewport = useIsInViewport(viewportRef);

  useEffect(() => {
    if (isInViewport) {
      setActiveLink({
        ...activeLink,
        banner: false,
        intro: false,
        lore: false,
        features: false,
        gravitars: true,
        roadmap: false,
        cta: false,
      });
    }
  }, [isInViewport]);

  return (
    <div id="gravitars" className="all-features-sec gravitors-sec">
      <div className="feature-sec feature-even-sec">
        <div className="container">
          <div className="feature-heading-sec">
            <h2 ref={viewportRef}>
              Your <span>Digital</span> self <span>Avaits</span> you in
              Gravitale
            </h2>
          </div>
          <div className="feature-desc">
            <TypeWriterEffect text="The Gravitar Species Is <span>Unique Blend</span> Of Organic And <br /> <span>Artificial</span> Components, Possessing Both The <br /> <span>Physical Abilities</span> And <br /> <span>Consciousness</span> Of Living Beings, As Well As The <br /> <span>Computational Power</span> And <br /> <span>Durability</span> Of Machines." />
          </div>
          <h1 className="placeholder-text">GRAVITARS</h1>
        </div>
        {/* <img src={dotspattern} className="dots-pattern" alt="" /> */}
        <DotsAnimation />
        <img src={tringleicon} className="tringleicon" alt="" />
      </div>
    </div>
  );
}

export default Gravitars;
