import React, { useEffect, useRef, useState } from "react";
import thumbnailimg from "../../assets/images/video-thumbnail-img.png";
import playbtn from "../../assets/images/play-btn-icon.png";
import tringleicon from "../../assets/images/pink-tringleicon.png";
import { Modal, ModalBody } from "reactstrap";
import dotspattern from "../../assets/images/dots-pattern.png";
import closeicon from "../../assets/images/close-icon.png";
import { useIsInViewport } from "../../helpers/helper";
import samplevideo from "../../assets/videos/sample-video.mp4";
import TypeWriterEffect from "../TypeWriterEffect/TypeWriterEffect";
import DotsAnimation from "../DotsAnimation/DotsAnimation";

function Lore({ setActiveLink, activeLink }) {
  const viewportRef = useRef(null);
  const videoRef = useRef(null);
  const isInViewport = useIsInViewport(viewportRef);

  useEffect(() => {
    if (isInViewport) {
      setActiveLink({
        ...activeLink,
        banner: false,
        intro: false,
        lore: true,
        features: false,
        gravitars: false,
        roadmap: false,
        cta: false,
      });
    }
  }, [isInViewport]);

  // Modal open state
  // const [modal, setModal] = useState(false);

  const [isVideoPlaying, setIsVideoPlaying] = useState(false);

  const playVideo = () => {
    // setModal(!modal);
    if (isVideoPlaying) {
      videoRef.current.pause();
    } else {
      videoRef.current.play();
      document.getElementById("loreVideo").setAttribute("controls", "");
    }
    setIsVideoPlaying(!isVideoPlaying);
  };

  return (
    <div id="lore" className="lore-sec">
      <div className="container">
        <div className="lore-desc">
          <TypeWriterEffect
            className="lore-heading-text"
            text="Once upon a time, in the far reaches of the universe, <br /> there was a <span>unique galaxy</span> with a <span>curious evolution</span>."
          />
          <h1 ref={viewportRef} className="placeholder-text">LORE</h1>
        </div>
        <div className="lore-video-sec">
          {/* <img src={thumbnailimg} /> */}
          <a
            onClick={playVideo}
            className={isVideoPlaying ? "d-none" : ""}
          >
            <img src={playbtn} className="playbtn" alt="" />
          </a>
          <video
            id="loreVideo"
            ref={videoRef}
            width="80%"
            muted
            poster={thumbnailimg}
            onEnded={() => {
              setIsVideoPlaying(false);
            }}
          >
            <source src={samplevideo} type="video/mp4" />
          </video>
        </div>
      </div>
      {/* <Modal isOpen={modal} toggle={toggle}>
        <ModalBody>
          <div className="video-popup">
            <iframe
              width="100%"
              height="345"
              src="https://www.youtube.com/embed/tgbNymZ7vqY"
            ></iframe>
            <button className="close-btn" onClick={toggle}>
              <img src={closeicon} />
            </button>
          </div>
        </ModalBody>
      </Modal> */}
      <img src={tringleicon} className="tringleicon" />
      {/* <img src={dotspattern} className="dots-pattern" /> */}
      <DotsAnimation />
    </div>
  );
}

export default Lore;
