import React, { useEffect, useRef, useState } from "react";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  NavbarText,
} from "reactstrap";
import logo from "../../assets/images/logo.png";

import AnchorLink from "react-anchor-link-smooth-scroll";
import { handleMouseClick, handleMouseEnter } from "../../helpers/helper";

function Header({ activeLink }) {
  const [isOpen, setIsOpen] = useState(false);
  const toggle = () => {
    if (window.innerWidth < 992) {
      isOpen
        ? document.getElementById("body").classList.remove("active")
        : document.getElementById("body").classList.add("active");
      setIsOpen(!isOpen);
    }
  };
  const [scroll, setScroll] = useState(false);
  useEffect(() => {
    window.addEventListener("scroll", () => {
      setScroll(window.scrollY > 50);
    });
  }, []);

  const handleClick = () => {
    handleMouseClick();
    toggle();
  };

  return (
    <div>
      <Navbar expand="lg" className={scroll ? "header sticky" : "header"}>
        <NavbarBrand>
          <img src={logo} alt="" />
        </NavbarBrand>
        <NavbarToggler
          onClick={toggle}
          className={isOpen === true ? "toggle-btn-open" : ""}
        >
          <span></span>
          <span></span>
          <span></span>
        </NavbarToggler>
        <div
          onClick={toggle}
          className={isOpen ? "toggler-div" : "d-none"}
        ></div>
        <div className="container">
          <Collapse isOpen={isOpen} navbar>
            <Nav className="me-auto" navbar>
              <NavItem className={activeLink.banner ? "active" : ""}>
                <NavLink href="/components/">
                  <AnchorLink
                    onMouseEnter={handleMouseEnter}
                    onClick={handleClick}
                    href="#banner"
                  >
                    <span>Hero</span>
                  </AnchorLink>
                </NavLink>
              </NavItem>
              <NavItem className={activeLink.intro ? "active" : ""}>
                <NavLink>
                  <AnchorLink
                    onMouseEnter={handleMouseEnter}
                    onClick={handleClick}
                    href="#intro"
                  >
                    <span>INTRODUCTION</span>
                  </AnchorLink>
                </NavLink>
              </NavItem>
              <NavItem className={activeLink.lore ? "active" : ""}>
                <NavLink>
                  <AnchorLink
                    onMouseEnter={handleMouseEnter}
                    onClick={handleClick}
                    href="#lore"
                  >
                    <span>LORE</span>
                  </AnchorLink>
                </NavLink>
              </NavItem>
              <NavItem className={activeLink.features ? "active" : ""}>
                <NavLink>
                  <AnchorLink
                    onMouseEnter={handleMouseEnter}
                    onClick={handleClick}
                    href="#features"
                  >
                    <span>FEATURES</span>
                  </AnchorLink>
                </NavLink>
              </NavItem>
              <NavItem className={activeLink.gravitars ? "active" : ""}>
                <NavLink>
                  <AnchorLink
                    onMouseEnter={handleMouseEnter}
                    onClick={handleClick}
                    href="#gravitars"
                  >
                    <span>GRAVITARS</span>
                  </AnchorLink>
                </NavLink>
              </NavItem>
              <NavItem
                className={activeLink.roadmap || activeLink.cta ? "active" : ""}
              >
                <NavLink>
                  <AnchorLink
                    onMouseEnter={handleMouseEnter}
                    onClick={handleClick}
                    href="#roadmap"
                  >
                    <span>ROADMAP</span>
                  </AnchorLink>
                </NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </div>
        <NavLink
          onMouseEnter={handleMouseEnter}
          onClick={handleMouseClick}
          href="https://www.google.com/"
          target="_blank"
          className="common-btn"
        >
          Join Us
        </NavLink>
      </Navbar>
    </div>
  );
}

export default Header;
