import React, { useEffect, useRef, useState } from "react";
import Scene from "../../components/Models/Scene";
import Banner from "../../components/Bannaer/Banner";
import CTA from "../../components/CTA/CTA";
import Features from "../../components/Features/Features";
import Gravitars from "../../components/Gravitars/Gravitars";
import Introduction from "../../components/Introduction/Introduction";
import Lore from "../../components/Lore/Lore";
import Roadmap from "../../components/Roadmap/Roadmap";
import Header from "../../components/Header/Header";
import Footer from "../../components/Footer/Footer";
import "bootstrap/dist/css/bootstrap.css";
import "../../assets/scss/common.scss";
import LoaderVideo from "../../components/LoaderVideo/LoaderVideo";
import AnchorLink from "react-anchor-link-smooth-scroll";
import sidebararrow from "../../assets/images/sidebar-arrow.png";
import tooltipImage from "../../assets/images/click-here-sound-img.png";
import { handleMouseEnter } from "../../helpers/helper";

function Home() {
  const [activeLink, setActiveLink] = useState({
    banner: true,
    intro: false,
    lore: false,
    features: false,
    gravitars: false,
    roadmap: false,
    cta: false,
  });

  const [isShowTooltip, setTooltipVisibility] = useState(true);

  const makeTooltipFollow = (e) => {
    const x = e.clientX;
    const y = e.clientY;
    document.getElementById("audio-tooltip").style.left = x + "px";
    document.getElementById("audio-tooltip").style.top = y + "px";
  };

  const handleMouseClick = () => {
    if (isShowTooltip) {
      setTooltipVisibility(false);
      document.getElementById("bg-audio").play();
      // document.getElementById("body").classList.remove("active");
    }
  };

  const bgaudioRef = useRef(null);

  const scroll = useRef(0);
  const divRef = useRef(null);
  const handleScroll = (e) => {
    scroll.current =
      window.scrollY / (divRef.current.scrollHeight - window.innerHeight);
  };
  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    window.onbeforeunload = function () {
      window.scrollTo(0, 0);
    }
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };

  }, []);

  return (
    <>
      <LoaderVideo />
      <div
        ref={divRef}
        className="canvas-sec"
        style={{ overflowX: "hidden" }}
        onMouseMove={makeTooltipFollow}
        onClick={handleMouseClick}
      >
        <div className={!isShowTooltip ? "d-none" : ""} id="audio-tooltip">
          <img src={tooltipImage} alt="" />
        </div>
        <Header activeLink={activeLink} />
        <Banner
          setActiveLink={setActiveLink}
          activeLink={activeLink}
          bgaudioRef={bgaudioRef}
        />
        <Introduction setActiveLink={setActiveLink} activeLink={activeLink} />
        <Lore setActiveLink={setActiveLink} activeLink={activeLink} />
        <Features setActiveLink={setActiveLink} activeLink={activeLink} />
        <Gravitars setActiveLink={setActiveLink} activeLink={activeLink} />
        <Roadmap setActiveLink={setActiveLink} activeLink={activeLink} />
        <CTA setActiveLink={setActiveLink} activeLink={activeLink} />
        <Footer />
        <Scene scroll={scroll} />
        <div
          className="patternbg"
          style={{
            backgroundImage:
              "url(' + patternbg + ')no-repeat center center/cover",
          }}
        ></div>
        {/* <img src={patternbg} className='patternbg' /> */}
        <ul className="sidebar-icon-list">
          <li
            onMouseEnter={handleMouseEnter}
            onMouseDown={handleMouseClick}
            className={activeLink.banner ? "active" : ""}
          >
            <AnchorLink href="#banner"></AnchorLink>
            <img src={sidebararrow} alt="" />
            <span>HERO</span>
          </li>
          <li
            onMouseEnter={handleMouseEnter}
            onMouseDown={handleMouseClick}
            className={activeLink.intro ? "active" : ""}
          >
            <AnchorLink href="#intro"></AnchorLink>
            <img src={sidebararrow} alt="" />
            <span>INTRODUCTION</span>
          </li>

          <li
            onMouseEnter={handleMouseEnter}
            onMouseDown={handleMouseClick}
            className={activeLink.lore ? "active" : ""}
          >
            <AnchorLink href="#lore"></AnchorLink>
            <img src={sidebararrow} alt="" />
            <span>LORE</span>
          </li>

          <li
            onMouseEnter={handleMouseEnter}
            onMouseDown={handleMouseClick}
            className={activeLink.features ? "active" : ""}
          >
            <AnchorLink href="#features"></AnchorLink>
            <img src={sidebararrow} alt="" />
            <span>FEATURES</span>
          </li>

          <li
            onMouseEnter={handleMouseEnter}
            onMouseDown={handleMouseClick}
            className={activeLink.gravitars ? "active" : ""}
          >
            <AnchorLink href="#gravitars"></AnchorLink>
            <img src={sidebararrow} alt="" />
            <span>GRAVITARS</span>
          </li>

          <li
            onMouseEnter={handleMouseEnter}
            onMouseDown={handleMouseClick}
            className={activeLink.roadmap || activeLink.cta ? "active" : ""}
          >
            <AnchorLink href="#roadmap"></AnchorLink>
            <img src={sidebararrow} alt="" />
            <span>ROADMAP</span>
          </li>
        </ul>
      </div>
    </>
  );
}

export default Home;
