import React, { useState } from "react";
import logo from "../../assets/images/logo.png";
import webLoaderVideo from "../../assets/videos/Gravitale_Placeholder_LogoAnimation.mp4";
import mobileLoaderVideo from "../../assets/videos/Gravitale_Placeholder_LogoAnimation_Mobile.mp4";

function LoaderVideo() {
  const [isVideoEnded, setVideoEnded] = useState(false);
  const [isVideoStarted, setVideoStarted] = useState(false);
  const [loader, setLoader] = useState(true);
  const handleVideoEnded = () => {
    setVideoEnded(true);
    window.scrollTo(0, 0);
    document.getElementById("body").classList.remove("active");
  };
  const hideLoader = () => setLoader(false);
  window.addEventListener("load", (event) => {
    document.getElementById("loader-video").play();
  });
  return (
    <div>
      <div style={{ display: loader ? "flex" : "none" }} className="loader-sec">
        <img src={logo} alt="" />
        <p>Loading...</p>
      </div>
      <div
        className={
          isVideoStarted ? "loader-video-sec active" : "loader-video-sec"
        }
        style={{ display: isVideoEnded ? "none" : "flex" }}
      >
        <video
          className="loader-video"
          id="loader-video"
          src={window.innerWidth < 767 ? mobileLoaderVideo : webLoaderVideo}
          muted={true}
          autoPlay={true}
          playsInline
          onEnded={handleVideoEnded}
          onLoadStart={hideLoader}
        ></video>
      </div>
    </div>
  );
}

export default LoaderVideo;
